import React from "react";
import { Lightbox } from "yet-another-react-lightbox";
import "yet-another-react-lightbox/styles.css";
import slides from "../assets/gallery/slides";

function Gallery() {
  const [index, setIndex] = React.useState(-1);

  const rows = [];
  for (let i = 0; i < slides.length; i += 3) {
    rows.push(slides.slice(i, i + 3));
  }

  return (
    <div className="flex w-[80%] md:w-[60%] lg:w-[50%] xl:w-[40%]">
      <div className="container mx-auto px-5 py-2 lg:px-32 lg:pt-12">
        <div className="-m-1 flex flex-wrap md:-m-2">
          {rows.map((row, rowIndex) => (
            <div
              key={rowIndex}
              className={`flex ${
                row.length < 3 ? "justify-center" : ""
              } w-full flex-wrap`}
            >
              {row.map((slide, currentIndex) => (
                <div
                  key={currentIndex}
                  className="flex w-1/3 flex-wrap"
                  onClick={() => setIndex(rowIndex * 3 + currentIndex)} // Calculate global index
                >
                  <div className="w-full p-1 md:p-2">
                    <img
                      alt={`gallery-${rowIndex * 3 + currentIndex}`}
                      className="block h-full w-full rounded-lg object-cover object-center cursor-pointer"
                      src={slide.src}
                    />
                  </div>
                </div>
              ))}
            </div>
          ))}
        </div>
      </div>
      <Lightbox
        index={index}
        slides={slides}
        open={index >= 0}
        close={() => setIndex(-1)}
      />
    </div>
  );
}

export default Gallery;
