import { React, useState, useEffect } from "react";

import { Trans } from "@lingui/react/macro";
import { i18n } from "@lingui/core";
import { messages as enMessages } from "../locales/en/messages";
import { messages as esMessages } from "../locales/es/messages";

function Navbar() {
  const [menuOpen, setMenuOpen] = useState(false);
  const [scrollingDown, setScrollingDown] = useState(false);

  const scrollToSection = (id) => {
    document.getElementById(id).scrollIntoView({ behavior: "smooth" });
    setMenuOpen(false);
  };

  const handleScroll = () => {
    if (window.scrollY > 50) {
      setScrollingDown(true);
    } else {
      setScrollingDown(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const locales = {
    en: enMessages,
    es: esMessages,
  };

  const handleLanguageChange = (event) => {
    const selectedLocale = event.target.value;

    i18n.load(selectedLocale, locales[selectedLocale]);
    i18n.activate(selectedLocale);
  };

  return (
    <div className="sti bg-black text-white relative z-50">
      <nav className="sticky flex top-0 w-full pt-4 shadow-lg justify-center items-center z-50">
        <div className="container font-serif w-full flex justify-between items-center px-4">
          {/* Logo */}
          <div className=""></div>

          {/* Hamburger Menu */}
          <button
            className="block md:hidden text-2xl"
            onClick={() => setMenuOpen(!menuOpen)}
          >
            ☰
          </button>

          {/* Navigation Links */}
          <ul
            className={`flex flex-col md:flex-row justify-center items-center flex-grow md:static absolute top-full left-0 right-0 bg-black md:bg-transparent transition-all duration-300 ${
              menuOpen ? "opacity-100 visible" : "opacity-0 invisible"
            } md:opacity-100 md:visible`}
          >
            <li className="px-8 text-xl py-2 md:py-0">
              <button onClick={() => scrollToSection("home")}>
                <Trans>Home</Trans>
              </button>
            </li>
            <li className="px-8 text-xl py-2 md:py-0">
              <button onClick={() => scrollToSection("socials")}>
                <Trans>Socials</Trans>
              </button>
            </li>
            <li className="px-8 text-xl py-2 md:py-0">
              <button onClick={() => scrollToSection("roadmap")}>
                <Trans>Roadmap</Trans>
              </button>
            </li>
            <li className="px-8 text-xl py-2 md:py-0">
              <button onClick={() => scrollToSection("credits")}>
                <Trans>Credits</Trans>
              </button>
            </li>
            {/* Language Selector for Mobile*/}
            {menuOpen && (
              <div className="px-8 text-xl py-2 md:py-0 bg-black p-4 md:hidden">
                <select
                  className="bg-black text-white border border-white p-2 rounded w-full"
                  onChange={handleLanguageChange}
                  defaultValue={i18n.locale}
                >
                  <option value="en">English</option>
                  <option value="es">Español</option>
                </select>
              </div>
            )}
          </ul>

          {/* Language Selector */}
          <div
            className={`transition-opacity duration-300 ease-in-out hidden md:block ${
              scrollingDown ? "opacity-0" : "opacity-100"
            } absolute ml-[70%]`}
          >
            <select
              className="bg-black text-white border border-white p-2 rounded"
              onChange={handleLanguageChange}
              defaultValue={i18n.locale}
            >
              <option value="en">English</option>
              <option value="es">Español</option>
            </select>
          </div>
        </div>
      </nav>

      <hr className="h-px mt-4 w-1/3 mx-auto border-t-0 bg-transparent bg-gradient-to-r from-transparent via-white to-transparent opacity-50 dark:via-white" />
    </div>
  );
}

export default Navbar;
