/*eslint-disable*/module.exports={messages:JSON.parse("{\"eSKZ5q\":[\"Arte 2D\"],\"j/Jfwj\":[\"Arte 3D\"],\"lIINOh\":[\"Un sueño, una pesadilla, una obsesión, una estrella solitaria, una grieta, una esperanza, una identidad, todo está aquí, todo terminará aquí, el tiempo corre.\"],\"uyJsf6\":[\"Sobre\"],\"rSyiAO\":[\"OTRA VEZ Y OTRA VEZ Y OTRA VEZ Y OTRA VEZ Y OTRA VEZ Y OTRA VEZ Y OTRA VEZ Y OTRA VEZ Y OTRA VEZ Y OTRA VEZ Y OTRA VEZ\"],\"sGxHeV\":[\"Todos los derechos reservados\"],\"Br6wzg\":[\"Únete a Patreon\"],\"jfC/xh\":[\"Contacto\"],\"Zc36ca\":[\"Liderazgo Creativo, Arte, Desarrollo, Programación, Narrativa y Dirección\"],\"lDIOek\":[\"Créditos\"],\"b1HtHW\":[\"Jugabilidad & Diseño de Niveles\"],\"i0qMbr\":[\"Inicio\"],\"4wqydg\":[\"C..ONFI..O E..N TI, P..ER.O AC..AS.O P..UED.E..S O.IR....ME\"],\"2hSxrZ\":[\"En RITUALICA, deberás hacer frente a los peligros arcanos de <0>su</0> mundo y más allá. Encerrada en está habitación, deberás aprender a controlar las Artes Ritualisticas y dominar la construcción de mazos para poder superar las amenazas que te depara el futuro. Cada Ritual será diferente, y cada decisión te acerca más y más hacia... <1>un final?</1>\"],\"OQwgiD\":[\"es un roguelite narrativo de construcción de mazos desarrollado por <0>AQUELARRE</0>, un pequeño equipo en Seville, España\"],\"r2v46c\":[\"Composición & Diseño de Sonido\"],\"FGVY9Z\":[\"Coger el teléfono\"],\"/gaSVU\":[\"Itinerario\"],\"s2ck0Z\":[\"Redes\"],\"5RnQ+x\":[\"Este <0>JUEGO</0> ha sido desarrollado gracias al duro trabajo de los miembros de <1>AQUELARRE</1>.\"],\"HgFl3c\":[\"Te despiertas en la oscuridad, encontrando un mundo al borde del colapso, solo sabes una cosa con claridad, debes completar el <0>RITUAL</0>. Descubre un mundo de misterio y peligros, donde cada momento te acerca más y más al fin, y las decisiónes que hagas pueden salvaros o condenaros a todos. Ella ha confiado todo en ti, su pasado ya olvidado, su eterno presente, y la breveddad de su futuro. No puedes fallar.\"],\"/s9Re5\":[\"Añade a la lista de deseados en steam!\"],\"VSGyOX\":[\"Encendiste la anciana maquina, una reliquia de tiempos ya olvidados. La pantalla parpadea, el protocolo comienza, se ha establecido una conexión.\"]}")};