const images = require.context(".", false, /\.(png|jpe?g|svg)$/);

export const slides = images.keys().map((imagePath) => {
  const src = images(imagePath);
  return {
    src,
    width: 3840, // Default width, adjust as needed
    height: 5070, // Default height, adjust as needed
  };
});

export default slides;
