import React, { useState, useEffect } from "react";
import { Parallax, ParallaxProvider } from "react-scroll-parallax";

import homeBackground from "../../assets/homeBackground.png";
import gameTitle from "../../assets/pngs/gameTitle.png";

function Head() {
  const [scrollingDown, setScrollingDown] = useState(false);

  const handleScroll = () => {
    if (window.scrollY > 50) {
      setScrollingDown(true);
    } else {
      setScrollingDown(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <ParallaxProvider>
      <Parallax speed={-25}>
        <div
          className="z-0 min-h-screen flex flex-col items-center align-top w-full h-full bg-cover bg-center bg-fixed"
          style={{ backgroundImage: `url(${homeBackground})` }}
        >
          <div
            className={`transition-opacity duration-500 ease-in-out ${scrollingDown ? "opacity-0" : "opacity-100"
              }`}
          >
            <img src={gameTitle} alt="Game Title" className="mt-[-15%]" />
          </div>
        </div>
      </Parallax>
    </ParallaxProvider>
  );
}

export default Head;
